@import 'smooth-text';

@mixin featured-flag {
  @include smooth-text;

  background-color: $color--olive-drab;
  border-radius: 0 0 2px 2px;
  color: $color--white;
  display: block;
  font: 1.3rem/1 $font-family--kapra-expanded;
  height: unit(1);
  left: unit(1);
  padding: 4px 6px 0;
  position: absolute;
  text-transform: uppercase;
  top: 0;
}
