.story-card__link {
  align-items: center;
  background: linear-gradient(
    to bottom,
    $color--jelly-bean 0%,
    $color--sherpa-blue 100%
  );
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 2px;
  color: $color--white;
  display: flex;
  font-size: 1.3rem;
  font-weight: $font-weight--normal;
  justify-content: center;
  min-height: 340px;
  overflow: hidden;
  padding: unit(1);
  position: relative;
  text-align: center;
  text-decoration: none;

  &::after,
  &::before {
    @include pseudo;

    border-radius: 2px;
    height: 100%;
    left: 0;
    top: 0;
    transition: all 0.2s $ease--snappy;
    width: 100%;
    z-index: 0;
  }

  &::after {
    background: linear-gradient(
      to bottom,
      rgba($color--fire, 0) 0.3%,
      rgba($color--fire, 0.8) 70%
    );
    opacity: 0;
  }

  &.-has-image {
    align-items: flex-end;

    &::before {
      background: linear-gradient(
        to bottom,
        rgba($color--tuatara, 0) 0.3%,
        rgba($color--tuatara, 0.9) 70%
      );
    }
  }

  @include respond-to('medium-large') {
    height: 100%;
    min-height: 450px;
  }

  .-big & {
    align-items: flex-end;

    @include respond-to('medium-large') {
      padding-right: 20%;
      text-align: left;
    }

    @include respond-to('x-large') {
      padding-right: 30%;
    }
  }

  @include hover {
    &::before {
      opacity: 0;
    }

    &::after {
      opacity: 1;
    }

    .story-card__content {
      transform: translate3d(0, 0, 0);
    }

    .story-card__faux-link {
      opacity: 1;
      transform: translate3d(0, 0, 0);
      transition: opacity 0.3s $ease--snappy 0.2s,
        transform 0.3s $ease--snappy 0.2s;
    }
  }

  .-big &,
  .-has-image & {
    @include hover {
      .story-card__content {
        transform: translate3d(0, calc(#{unit(-0.5)} + 2px), 0);
      }
    }
  }
}

.story-card__flag {
  @include featured-flag;
}

.story-card__content {
  @include null-margins;

  position: relative;
  transform: translate3d(0, calc(#{unit(1.5)} + 2px), 0);
  transition: transform 0.3s $ease--snappy;
  z-index: 1;

  .-big &,
  .-has-image & {
    transform: translate3d(0, calc(#{unit(2.5)} + 2px), 0);
  }
}

.story-card__heading {
  @include smooth-text;

  font: 700 3rem/1.3 $font-family--serif;

  .-has-image & {
    font-size: 2rem;
  }

  @include respond-to('medium-large') {
    .-big & {
      font-size: 2.2rem;
    }
  }
}

.story-card__subheading {
  display: block;
  font: 700 1.3rem/1.3 $font-family--kapra-expanded;
  margin-bottom: unit(0.5);
  text-transform: uppercase;
}

.story-card__excerpt {
  display: none;

  .-big & {
    display: block;
  }
}

.story-card__date {
  color: rgba($color--white, 0.8);
  font-size: 1.2rem;
}

.story-card__faux-link {
  border: 1px solid $color--white;
  border-radius: 2px;
  display: block;
  font-weight: $font-weight--bold;
  line-height: unit(1.5);
  margin: unit(1) auto 0;
  max-width: unit(10);
  opacity: 0;
  overflow: hidden;
  position: relative;
  text-align: center;
  transform: translate3d(0, unit(1), 0);
  transition: opacity 0.3s $ease--snappy, transform 0.3s $ease--snappy;

  @include respond-to('medium-large') {
    .-big & {
      margin-left: 0;
      margin-right: 0;
    }
  }
}
